import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Routes, NavLink } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Button, Menu, MenuItem, Box, ThemeProvider, createTheme } from '@mui/material';
import { ArrowDropDown } from '@mui/icons-material';
import Home from './pages/Home';
import About from './pages/About';
import Dashboard from './pages/Dashboard';
import Settings from './pages/Settings';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,  // For dropdown menu
    };
  }

  handleMenuClick = (event) => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleMenuClose = () => {
    this.setState({ anchorEl: null });
  };

  render() {
    // Create a dark theme
    const theme = createTheme({
      palette: {
        mode: 'dark',
      },
    });

    return (
      <ThemeProvider theme={theme}>
        <Router>
          <div>
            {/* Header with Navigation Links */}
            <AppBar position="static">
              <Toolbar sx={{ display: 'flex', justifyContent: 'space-between' }}>
                <Typography variant="h6" sx={{ flexGrow: 1 }}>
                  BOARD 
                </Typography>

                {/* Main Menu Buttons */}
                <Box sx={{ display: 'flex', justifyContent: 'flex-start', flexGrow: 2 }}>
                  <Button color="inherit" component={NavLink} to="/" exact activeClassName="active">
                    Home
                  </Button>
                  <Button color="inherit" component={NavLink} to="/about" activeClassName="active">
                    About
                  </Button>

                  {/* Dashboard Menu with Submenu */}
                  <Button
                    color="inherit"
                    onClick={this.handleMenuClick}
                    endIcon={<ArrowDropDown />}
                    aria-controls="dashboard-menu"
                    aria-haspopup="true"
                  >
                    Dashboard
                  </Button>
                  <Menu
                    id="dashboard-menu"
                    anchorEl={this.state.anchorEl}
                    open={Boolean(this.state.anchorEl)}
                    onClose={this.handleMenuClose}
                  >
                    <MenuItem onClick={this.handleMenuClose} component={NavLink} to="/dashboard">
                      Main Dashboard
                    </MenuItem>
                    <MenuItem onClick={this.handleMenuClose} component={NavLink} to="/dashboard/settings">
                      Settings
                    </MenuItem>
                  </Menu>
                </Box>
              </Toolbar>
            </AppBar>

            {/* AppBar     */}
            <div style={{ height: '64px', backgroundColor: '#121212' }}></div>
            {/* Main Content with dark background and white text */}
            <div style={{ backgroundColor: '#121212', color: 'white', padding: '20px', minHeight: '100vh' }}>
              <Routes>
                <Route path="/" element={<Home />} />
                <Route path="/about" element={<About />} />
                <Route path="/dashboard" element={<Dashboard />} />
                <Route path="/dashboard/settings" element={<Settings />} />
              </Routes>
            </div>
          </div>
        </Router>
      </ThemeProvider>
    );
  }
}

export default App;
